import {Component, Input, OnInit} from '@angular/core';
import {LineOption} from "./overall-analysis-chart.option";
import {OverallAnalysisService} from "../service/overall-analysis-service.";

@Component({
    selector: 'app-overall-analysis-chart',
    templateUrl: './overall-analysis-chart.component.html',
    styleUrls: ['./overall-analysis-chart.component.scss']
})
export class OverallAnalysisChartComponent implements OnInit {


   @Input() trendLineOption: LineOption;



    // events
    public chartClicked(e: any): void {
        console.log(e);
    }

    public chartHovered(e: any): void {
        console.log(e);
    }



    constructor(private overallAnalysisService: OverallAnalysisService) {
    }

    ngOnInit() {
        // this.trendLineOption = this.overallAnalysisService.buildTrendLineOption(this.chartData);
    }

    metricChangeAction(key : string){
        console.log("metricChangeAction:" + key);
        let chartDataChanged = {
            "xAxis": ['1月','2月','3月','4月','5月','6月','7月','8月','9月','10月','11月','12月'],
            "series": [{
                "groupId": "emg",
                "name": "2016月总单量",
                "series": [1,2,3,4,5,6,7,8,5,3,1,2]
            },{
                "groupId": "emg",
                "name": "2017月总单量",
                "series": [12,23,38,42,54,69,71,83,15,23,11,32]
            }]
        };
        this.trendLineOption = this.overallAnalysisService.buildTrendLineOption(chartDataChanged);

    }

}




