import {
    Component, Input, OnChanges, OnInit, SimpleChanges, ChangeDetectionStrategy,
    ChangeDetectorRef
} from '@angular/core';
import {IlOverallAnalysisTable} from "../model/il-overall-analysis.table";
import {OverallAnalysisService} from "../service/overall-analysis-service.";

@Component({
  selector: 'app-overall-analysis-table',
  templateUrl: './overall-analysis-table.component.html',
  styleUrls: ['./overall-analysis-table.component.scss']
})
export class OverallAnalysisTableComponent implements OnInit,OnChanges {

    ngOnChanges(changes: SimpleChanges): void {
        // console.dir("changes : " + JSON.stringify(changes));
    }

    data = [
        11
    ];

    private rowsLastYear : Array<any>;

    private columnsLastYear : Array<any>;

    private columnsThisYear: Array<any>;

    private rowsThisYear: Array<any>;

    @Input() ilOverallAnalysisTable : IlOverallAnalysisTable = new IlOverallAnalysisTable();


    constructor(private overallAnalysisService: OverallAnalysisService,private  cdrf: ChangeDetectorRef) {

    }




    metricChangeAction(key : string,dept: string){
        // console.log("metricChangeAction:" + key);
        this.overallAnalysisService.getTableMetrics(key,dept).then(table => {
            // console.log(this.ilOverallAnalysisTable);
            this.ilOverallAnalysisTable = this.overallAnalysisService.buildTable(table);
            this.cdrf.detectChanges();
            this.cdrf.markForCheck();
        });

    }


  ngOnInit() {
  }

}



