import {Component, OnInit, ViewChild} from '@angular/core';
import {Metric} from "./model/il-overall-analysis.metric";
import {routerTransition} from '../../router.animations';
import {OverallAnalysisTableComponent} from "./overall-analysis-table/overall-analysis-table.component";
import {OverallAnalysisChartComponent} from "./overall-analysis-chart/overall-analysis-chart.component";
import {IlOverallAnalysisTable} from "./model/il-overall-analysis.table";
import {LineOption} from "./overall-analysis-chart/overall-analysis-chart.option";
import {OverallAnalysisService} from "./service/overall-analysis-service.";
import {CONFIG} from '../../shared/services/config';
import {DataService} from '../../shared/services/data.service';
import {EchartsLineOption} from "./model/il-overall-chart.option";



@Component({
    selector: 'app-overall-analysis',
    templateUrl: './overall-analysis.component.html',
    styleUrls: ['./overall-analysis.component.scss'],
    animations: [routerTransition()]
})
export class OverallAnalysisComponent implements OnInit{
    option : EchartsLineOption;
    // LineEchartsOption : EchartsLineOption;

    @ViewChild(OverallAnalysisTableComponent)
    overallAnalysisTableComponent: OverallAnalysisTableComponent;

    @ViewChild(OverallAnalysisChartComponent)
    overallAnalysisChartComponent: OverallAnalysisChartComponent;


    private m: Metric[];

    ilOverallAnalysisTable: IlOverallAnalysisTable = new IlOverallAnalysisTable();

    trendLineOption: LineOption;

    constructor(private dataService: DataService, private overallAnalysisService: OverallAnalysisService) {

    }

    metricClickCasCade(key: string) {

        this.overallAnalysisTableComponent.metricChangeAction(key,this._defaultDeptValue);
        this.overallAnalysisService.getChartMetrics(key,this._defaultDeptValue)
            .then(chart => this.trendLineOption = this.overallAnalysisService.buildTrendLineOption(chart));
        this.overallAnalysisService.getEChartMetrics(key, this._defaultDeptValue).then(data => this.option = this.overallAnalysisService.buildOverAllYearLineOption(data.body));

    }

    private _defaultMetricKey = 'overnight_delivery_sales';
    private _defaultDept = [];
    private _defaultDeptValue = '';


    ngOnInit() {
        this.getDept();
    }

    // 获取部门
    getDept(){
        this.dataService.getJson(CONFIG.URL.dept)
            .then(data =>{
                if(data.success){
                    this._defaultDeptValue = data.body[0].value;
                    this._defaultDept = data.body;
                    this.overallAnalysisService.getBoardMetrics(this._defaultDeptValue).then(metrics => this.m = metrics);
                    this.overallAnalysisService.getTableMetrics(this._defaultMetricKey, this._defaultDeptValue).then(table => this.ilOverallAnalysisTable = this.overallAnalysisService.buildTable(table));
                    this.overallAnalysisService.getChartMetrics(this._defaultMetricKey, this._defaultDeptValue).then(chart => this.trendLineOption = this.overallAnalysisService.buildTrendLineOption(chart));
                    this.overallAnalysisService.getEChartMetrics(this._defaultMetricKey, this._defaultDeptValue).then(data => this.option = this.overallAnalysisService.buildOverAllYearLineOption(data.body));
                }
            });
    }


    deptChange(){
        this.overallAnalysisService.getBoardMetrics(this._defaultDeptValue).then(metrics => this.m = metrics);
        this.overallAnalysisService.getTableMetrics(this._defaultMetricKey, this._defaultDeptValue).then(table => this.ilOverallAnalysisTable = this.overallAnalysisService.buildTable(table));
        this.overallAnalysisService.getChartMetrics(this._defaultMetricKey, this._defaultDeptValue).then(chart => this.trendLineOption = this.overallAnalysisService.buildTrendLineOption(chart));
        this.overallAnalysisService.getEChartMetrics(this._defaultMetricKey, this._defaultDeptValue).then(data => this.option = this.overallAnalysisService.buildOverAllYearLineOption(data.body));
    }

}



