import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {Metric} from "../model/il-overall-analysis.metric";
import { NzMessageService } from "ng-zorro-antd";

import { CONFIG } from '../../../shared/services/config';
import { DataService } from '../../../shared/services/data.service';

@Component({
  selector: 'app-overall-analysis-board',
  templateUrl: './overall-analysis-board.component.html',
  styleUrls: ['./overall-analysis-board.component.scss']
})
export class OverallAnalysisBoardComponent implements OnInit {

   private up : Boolean = true;

   private down : Boolean = false;

  private current = 0;
  private showBox = false;
  private inputValue : Number ; //input的值
  private error : String = ''; //input的值
  private metricTemp;
  
  constructor(private dataService: DataService, private _message: NzMessageService) { }

  @Input() deptNo : string;

  @Input() metrics : Metric[];

  @Output() metricClick : EventEmitter<Metric> = new EventEmitter();



  onClick(metric : Metric, index){
      console.log("metric:",index);
      this.metricClick.emit({key:metric.key});
      this.current = index;
      this.metricTemp = metric;
  }

  //显示 编辑框
  showEdit(value,event){
    this.showBox = true; //显示编辑框
    this.inputValue = value; //赋予当前值

    event.stopPropagation();
  }

  //编辑框-保存
  saveEdit(event){
    event.stopPropagation();
    const val = (this.inputValue).toString();
    const reg = /^\d+(\.\d+)?$/;
    if(val.length === 0){
      this.error = '重量不能为空';
      return false;
    }
    const valTemp = Number(val);
    if(isNaN(valTemp)){
      this.error = '请输入正确的重量';
      return false;
    }
    if(valTemp > 999999999 || valTemp < 0){
        this.error = '请输入正确的重量';
        return false;
    }


    this.error = '';
    this.dataService.getJson(CONFIG.URL.save,{avgOrdWeight:val,dept:this.deptNo}).then(data => {
        if (data.success) {
          this.metricClick.emit({key:this.metricTemp.key});
          this._message.success('保存成功');
          this.showBox = false; 
          this.inputValue = data.body.newAvgOrdWeight;
          this.metrics[6].value =  String(this.inputValue) ;
        }else{
          this._message.error('保存失败：' + data.message,{nzDuration: 3000});
        }
    });
  }

  //编辑框-取消
  cancelEdit(event){
    this.showBox = false;
      this.error = '';
    event.stopPropagation();
  }

  ngOnInit() {
    
  }

}



