import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { OverallAnalysisComponent } from './overall-analysis.component';
import {OverallAnalysisRoutingModule} from "./overall-analysis.routing.module";
import { YscComponentModule } from 'ysc-pop-component';
import { OverallAnalysisChartComponent } from './overall-analysis-chart/overall-analysis-chart.component';
import {ChartsModule} from "ng2-charts";
import { OverallAnalysisBoardComponent } from './overall-analysis-board/overall-analysis-board.component';
import {PercentFormatPipe} from "../../shared/pipes/percent.format.pipe";
import {OverallAnalysisService} from "./service/overall-analysis-service.";
import { OverallAnalysisTableComponent } from './overall-analysis-table/overall-analysis-table.component';
import { NgZorroAntdModule } from "ng-zorro-antd";
import { NgxDatatableModule }   from "@swimlane/ngx-datatable";
import { AngularEchartsModule } from 'ngx-echarts';
import { FormsModule }   from '@angular/forms';


@NgModule({
  imports: [
    CommonModule,
    OverallAnalysisRoutingModule,
    YscComponentModule,
    ChartsModule,
    NgZorroAntdModule.forRoot(),
      NgxDatatableModule,
      AngularEchartsModule,
      FormsModule,
  ],
  declarations: [OverallAnalysisComponent, OverallAnalysisChartComponent, OverallAnalysisBoardComponent,PercentFormatPipe, OverallAnalysisTableComponent],
  providers:[OverallAnalysisService]
})
export class OverallAnalysisModule {


}



