import { Pipe, PipeTransform } from '@angular/core';
/*
 * Usage:
 *   value | percentFormat:fixed
 * Example:
 *   {{ 0.02 | percentFormat:1 }}
 *   formats to: 2.0%
*/
@Pipe({name: 'percentFormat'})
export class PercentFormatPipe implements PipeTransform {
  transform(value: number, fixed: string = "1"): string {
    let exp = parseFloat(fixed);
    return (value * 100).toFixed(exp) + "%";
  }
}


